body {
    font-family: Arial, Helvetica, sans-serif;
    position: relative;
    min-height: 100vh;
    margin: 0;
    padding: 0;
    font-family: 'Quicksand', sans-serif;

}


h1, h2, h3, h4, h5, h6 {
    font-family: 'Roboto Slab', serif;
    font-weight: 600;
    padding: 15px 0;
    text-align: center;
}

button {
    background-color: transparent!important;
    border: 2px solid #0E7373  !important;
    font-weight: 600 !important;
    color:  #0E7373 !important;
    padding: 10px ;
    margin: 10px ;
    border-radius: 10px !important;
    text-align: center !important;
}

.button {
    background-color:transparent  !important;
    border: 2px solid #0E7373  !important;
    font-weight: 600 !important;
    color:  #0E7373 !important;
    padding: 10px;
    margin: 10px ;
    max-width: 180px;
    border-radius: 10px !important;
    text-align: center !important;
}
hr {
    border-color: #C1D0D9 !important;
}